/*!* Question Component *!*/
.radioCustomButton {
    position: absolute;
    width: auto;
    opacity: 0
}

.radioCustomButton, .radioCustomLabel {
    display: inline-block;
    vertical-align: middle;
    cursor: pointer
}

.radioCustomLabel {
    position: relative;
    width: 100%;
    padding: 30px;
    height: 100%;
    display: flex;
    justify-content: space-between;
    text-align: left;
    flex-direction: column;
    font-size: 15px;
    line-height: 1.4;
    letter-spacing: 0;
    border: 1px solid #e6e6e6;
    cursor: pointer;
    transition-delay: .4s;
    transition: .2s linear;
    color: #474747
}

.radioCustomLabel span:first-letter {
    text-transform: uppercase
}

@media (max-width: 767px) {
    .radioCustomLabel {
        display: flex;
        flex-direction: row;
        padding: 10px;
        align-items: center;
        box-shadow: 0 20px 18px rgb(0 0 0/5%);
        border: 1px solid #fff;
        background-color: #fff
    }
}

.example-enter {
    opacity: .01
}

.example-enter.example-enter-active {
    opacity: 1;
    transition: opacity .2s
}

.example-leave {
    opacity: 1;
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    transition: opacity 0ms
}

.example-leave.example-leave-active {
    opacity: 0;
    top: 0;
    left: 0;
    display: none;
    position: absolute;
    transition: opacity .2s;
    transition-delay: .3s
}

.answerOptions {
    display: flex;
    justify-content: space-between;
    text-align: left
}

@media screen and (max-width: 767px) {
    .answerOptions {
        flex-wrap: wrap
    }
}

.answerOption {
    min-height: 325px;
    width: 47.5%;
    box-sizing: border-box;
    transition: .3s
}

.answerOption.disabled {
    pointer-events: none;
    width: 100%;
    height: auto
}

.answerOption.disabled .radioCustomLabel img {
    -webkit-filter: none;
    filter: none
}

@media screen and (max-width: 767px) {
    .answerOption {
        width: 100%;
        min-height: 0
    }

    .answerOption:first-child {
        margin-bottom: 20px
    }
}

.answerOption:hover .radioCustomLabel {
    box-shadow: 0 20px 18px rgb(0 0 0/5%);
    border: 1px solid #fff;
    background-color: #fff
}

.answerOption img {
    max-width: 150px;
    display: block;
    margin: 0 auto;
    -webkit-filter: grayscale(60%);
    filter: grayscale(60%)
}

@media screen and (max-width: 767px) {
    .answerOption img {
        order: -1;
        -webkit-filter: none;
        filter: none;
        width: 30%;
        margin-right: 10px;
        height: 100%;
    }
}

.answerOption:hover img {
    -webkit-filter: none !important;
    filter: none !important
}

